import { definition, Component } from '@eui/component';
// TODO: these should be dynamically imported.
import light from '../../../assets/themes/light.css';
import dark from '../../../assets/themes/dark.css';

const DEFAULT_THEMES = ['light', 'dark'];

/**
 * @property {string} theme - name of theme to load
 * @property {string} fontPath - specifies the font path to be used
 * @property {string} themePath - specifies the theme path to be used
 */
@definition(`eui-theme-v${__VERSION__}`, {
  props: {
    theme: { attribute: true, type: String, default: 'light' },
    fontPath: { attribute: true, type: String },
    themePath: { attribute: true, type: String },
  },
})
export default class Theme extends Component {
  constructor() {
    super();
    this._changeTheme = this._changeTheme.bind(this);
    this._fetchAlternativeTheme = this._fetchAlternativeTheme.bind(this);
  }

  /**
   * @function _replaceVariable
   * @param {String} source - the source string to modify
   * @param {String} variable - the name of the variable to replace
   * @param {String} replace - the value to replace
   */
  _replaceVariable(source, variable, replace) {
    const regExp = new RegExp(`{{${variable}}}`, 'g');
    source = source.toString().replace(regExp, replace);
    return source;
  }

  /**
   * Change the theme. After 100ms remove the old style. This
   * prevents a flashing of the page when the style changes.
   *
   * @function _changeTheme
   * @param {String} prevTheme - previous theme name
   * @param {String} theme - name of the theme
   * @private
   */
  _changeTheme(prevTheme, theme) {
    const fontPath = this.fontPath === null ? './@eui/theme/fonts/' : this.fontPath;
    if (!DEFAULT_THEMES.includes(theme)) {
      this._fetchAlternativeTheme(this.fontPath, this.themePath, theme, prevTheme)
        .catch((error) => {
          console.error(error.message);
        });
    } else {
      const styleTag = this._createEmptyStyleTag(theme);
      switch (theme) {
        case 'light':
          styleTag.innerHTML = this._replaceVariable(light, 'fontPath', fontPath);
          break;

        case 'dark':
          styleTag.innerHTML = this._replaceVariable(dark, 'fontPath', fontPath);
          break;

        default:
          break;
      }
      window.document.head.appendChild(styleTag);
      this._removeOldTheme(prevTheme);
    }
  }

  /**
   * Create a style tag with the id of the theme.
   * This new style tag will serve as a holder
   * for the new theme
   *
   * @function _createEmptyStyleTag
   * @param {String} theme - name of the theme
   * @returns Element
   */
  _createEmptyStyleTag(theme) {
    const styleTag = document.createElement('style');
    styleTag.setAttribute('id', `theme-${theme}`);
    styleTag.setAttribute('type', 'text/css');
    return styleTag;
  }

  /**
   * Remove the old theme from the head. The old theme is
   * removed after 100ms to avoid a flash of un-styled content
   *
   * @function _removeOldTheme
   * @param {String} oldTheme - name of old theme
   */
  _removeOldTheme(oldTheme) {
      setTimeout(() => {
        try {
          const prevStyleTag = window.document.head.querySelector(`#theme-${oldTheme}`);
          window.document.head.removeChild(prevStyleTag);
        } catch (error) {
          // ignore error.
        }
      }, 100);
  }

  /**
   * Lifecycle callback executed each time the components props are updated
   *
   * @method didChangeProps
   * @param {Map} changedProps - the previous values of the components changed props
   */
  didChangeProps(changedProps) {
    if (changedProps.has('theme') || (changedProps.size === 0)) {
      const theme = changedProps.get('theme');
      this._changeTheme(theme, this.theme);
    } else if (changedProps.has('fontPath')) {
      this._changeTheme(this.theme, this.theme);
    }
  }

  /**
   * Lifecycle callback executed when componments connects for the first time
   *
   * @method didConnect
   */
  didConnect() {
    this._changeTheme(null, this.theme);
  }

  /**
   * Fetch alternative theme from an alternative location. This is used to
   * fetch themes which are not the supplied themes of light and dark.
   * Alternative themes are loaded in the head within a style tag. Once added
   * the previous theme is removed.
   *
   * @function _fetchAlternativeTheme
   * @param {String} fontPath - path to the location of alternative font
   * @param {String} themePath - path to the location of alternative CSS
   * @param {String} theme - name of theme to apply
   * @param {String} prevTheme - name of previous theme
   * @private
   */
  async _fetchAlternativeTheme(fontPath, themePath, theme, prevTheme) {
    if (!themePath) {
      throw new Error(`Theme path is not set for custom theme: ${theme}`);
    }
    if (!theme) {
      throw new Error('Theme name is missing');
    }

    window.fetch(`${themePath}/${theme}.css`)
    .then((response) => {
      if (response.ok) {
        return response.text();
      }
      return Promise.reject(new Error(`Failed to load ${theme} theme`));
    })
    .then((data) => {
      const styleTag = this._createEmptyStyleTag(theme);
      fontPath = fontPath === null ? './@eui/theme/fonts/' : fontPath;
      styleTag.innerHTML = this._replaceVariable(data, 'fontPath', fontPath);
      window.document.head.appendChild(styleTag);
      this._removeOldTheme(prevTheme);
    })
    .catch((error) => {
      console.error(error.message);
      this._removeOldTheme(prevTheme);
    });
  }
}

// Register eui-theme component
Theme.register();
